import React, { useRef, useEffect } from 'react';
import CanvasDraw from 'react-canvas-draw';

const Canvas = ({ brushSize, onDrawing }) => {
    const canvasRef = useRef(null);

    useEffect(() => {
        const canvas = canvasRef.current;
        canvas.width = 750; // Đặt chiều rộng canvas
        canvas.height = 500; // Đặt chiều cao canvas
    }, []);

    const handleDraw = () => {
        if (!canvasRef.current) return;
        const drawingData = canvasRef.current.getSaveData();
        const canvas = canvasRef.current.canvasContainer.children[1]; // Canvas layer chính
        const dataUrl = canvas.toDataURL('image/png');
        onDrawing(dataUrl); // Cập nhật hình ảnh
    };

    const handleUndo = () => {
        if (canvasRef.current) {
            canvasRef.current.undo();
            handleDraw();
        }
    };

    const handleClearCanvas = () => {
        if (canvasRef.current) {
            canvasRef.current.clear();
            onDrawing(null); // Thông báo cho parent rằng canvas đã được xóa
        }
    };

    return (
        <div className="canvas-container">
            <CanvasDraw
                ref={canvasRef}
                brushColor="#000"
                brushRadius={brushSize}
                canvasWidth={750}
                canvasHeight={500}
                lazyRadius={0}
                onChange={handleDraw} // Gọi handleDraw mỗi khi có thay đổi
            />
            <button className="btn-undo" onClick={handleUndo}>UNDO</button>
            <button className="btn-clear" onClick={handleClearCanvas}>CLEAR</button>
        </div>
    );
};

export default Canvas;