import React, { useState } from 'react';
import Canvas from './Canvas';

function App() {
    const [brushSize, setBrushSize] = useState(5);
    const [suggestion, setSuggestion] = useState({ english: '', chinese: '', example: '', hint: '' });
    const [userDrawing, setUserDrawing] = useState(null);
    const [sampleDrawing, setSampleDrawing] = useState('');
    const [showCanvas, setShowCanvas] = useState(true);
    const [showImages, setShowImages] = useState(false);
    const [resultData, setResultData] = useState(null);
    const [hintState, setHintState] = useState(0);
    const [submitCount, setSubmitCount] = useState(0);

    const fetchNewQuestion = async () => {
        try {
            const response = await fetch('http://localhost:5000/get_new_question');
            if (!response.ok) {
                throw new Error('Network response was not ok');
            }
            const data = await response.json();
            setSuggestion({
                word: data.word,
                english: data.english_translation,
                chinese: data.example_with_blank,
                pinyin: data.pinyin,
                pos: data.pos,
                vietnamese: data.vietnamese,
                full_sentence: data.full_sentence,
                hint: '_ _'
            });

            drawSample(data.word);
            setShowCanvas(true);
            setShowImages(false);
            setResultData(null);
            setHintState(0); // Reset hint state
            setSubmitCount(0); // Reset submit count
        } catch (error) {
            console.error('Error fetching new question:', error);
        }
    };

    const drawSample = (sample) => {
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('2d');
        canvas.width = 750;
        canvas.height = 500;

        // Vẽ chữ mẫu
        context.fillStyle = 'black';
        context.font = '120px SimSun'; // Font tiếng Trung
        context.textBaseline = 'middle';
        context.textAlign = 'center';
        context.fillText(sample, canvas.width / 2, canvas.height / 2);

        // Lưu hình ảnh mẫu
        const sampleImage = canvas.toDataURL('image/png');
        setSampleDrawing(sampleImage); // Cập nhật state với hình ảnh mẫu
    };

    const handleSubmit = () => {
        const currentWord = suggestion.word;
        if (!userDrawing) {
            alert('Bạn chưa vẽ gì vào ô canvas. Vui lòng vẽ hoặc xem gợi ý.');
            if (currentWord.length === 1) {
                setSuggestion({ ...suggestion, hint: currentWord });
            } else if (currentWord.length === 2) {
                if (hintState === 0) {
                    setSuggestion({ ...suggestion, hint: `${currentWord[0]} _` });
                    setHintState(1);
                } else if (hintState === 1) {
                    setSuggestion({ ...suggestion, hint: currentWord });
                    setHintState(2);
                }
            }
            setSubmitCount(submitCount + 1);
            if ((currentWord.length === 1 && submitCount >= 1) || (currentWord.length === 2 && submitCount >= 2)) {
                setShowCanvas(false);
                setShowImages(true);
                setResultData({
                    word: suggestion.word,
                    pinyin: suggestion.pinyin,
                    pos: suggestion.pos,
                    vietnam: suggestion.vietnamese,
                    exChinese: suggestion.full_sentence,
                    tlEnglish: suggestion.english,
                });
            }
        } else {
            setShowCanvas(false);
            setShowImages(true);
            setResultData({
                word: suggestion.word,
                pinyin: suggestion.pinyin,
                pos: suggestion.pos,
                vietnam: suggestion.vietnamese,
                exChinese: suggestion.full_sentence,
                tlEnglish: suggestion.english,
            });
        }
    };

    const handleDrawing = (drawing) => {
        setUserDrawing(drawing);
    };

    const handleHint = () => {
        const currentWord = suggestion.word;
        if (currentWord) {
            if (currentWord.length === 1) {
                setSuggestion({ ...suggestion, hint: currentWord });
            } else if (currentWord.length === 2) {
                if (hintState === 0) {
                    setSuggestion({ ...suggestion, hint: `${currentWord[0]} _` });
                    setHintState(1);
                } else if (hintState === 1) {
                    setSuggestion({ ...suggestion, hint: currentWord });
                    setHintState(2);
                }
            }
        }
    };

    const handleSkip = () => {
        setShowCanvas(false);
        setShowImages(true);
        setResultData({
            word: suggestion.word,
            pinyin: suggestion.pinyin,
            pos: suggestion.pos,
            vietnam: suggestion.vietnamese,
            exChinese: suggestion.full_sentence,
            tlEnglish: suggestion.english,
        });
    };

    return (
        <div className="app-container">
            <h1>Luyện Viết Tiếng Trung</h1>
            <div className="main-content">
                {showCanvas && (
                    <div className="canvas-container">
                        <Canvas brushSize={brushSize} onDrawing={handleDrawing} />
                        <div className="brush-size-container">
                            <label htmlFor="brushSize">Kích thước nét vẽ: {brushSize}</label>
                            <input
                                type="range"
                                id="brushSize"
                                min="1"
                                max="20"
                                value={brushSize}
                                onChange={(e) => setBrushSize(e.target.value)}
                            />
                        </div>
                        <div className="controls">
                            <button className="btn-submit" onClick={handleSubmit}>NỘP</button>
                            <button className="btn-suggest" onClick={fetchNewQuestion}>CÂU HỎI MỚI</button>
                            <button className="btn-skip" onClick={handleSkip}>BỎ QUA</button>
                            <button className="btn-hint" onClick={handleHint}>GỢI Ý</button>
                        </div>
                    </div>
                )}
                {showImages && (
                    <div className="image-container">
                        <div className="user-drawing">
                            <h2>Nét Vẽ Của Bạn</h2>
                            {userDrawing && <img src={userDrawing} alt="User Drawing" style={{ width: '100%', height: 'auto' }} />}
                        </div>
                        <div className="sample-drawing">
                            <h2>Hình Mẫu</h2>
                            {sampleDrawing && <img src={sampleDrawing} alt="Sample Drawing" style={{ width: '100%', height: 'auto' }} />}
                        </div>
                    </div>
                )}
                <div className="info-container">
                    <div className="suggestion">
                        <p>Gợi ý: {suggestion.hint}</p>
                        <p>English: {suggestion.english}</p>
                        <p>Chinese: {suggestion.chinese}</p>
                    </div>
                    <button className="new-question" onClick={fetchNewQuestion}>CÂU HỎI MỚI</button>
                    <div className="results">
                        <h2>Kết Quả</h2>
                        <table className="result-table">
                            <thead>
                                <tr>
                                    <th>Word</th>
                                    <th>Pinyin</th>
                                    <th>Pos</th>
                                    <th>Vietnam</th>
                                    <th>Ex. Chinese</th>
                                    <th>TL. English</th>
                                </tr>
                            </thead>
                            <tbody>
                                {resultData && (
                                    <tr>
                                        <td>{resultData.word}</td>
                                        <td>{resultData.pinyin}</td>
                                        <td>{resultData.pos}</td>
                                        <td>{resultData.vietnam}</td>
                                        <td>{resultData.exChinese}</td>
                                        <td>{resultData.tlEnglish}</td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default App;